.intro-item{
	padding-top:$f15;
	position:absolute;
	top:80px;
	left:0;
	right:0;
	bottom:0px;
	background-color:#f8f8f8;
	.info{
		background-color:$white;
		border:1px solid #eaeaea;
		padding:$f15 0;
		margin-bottom:$f15;
		border-bottom:2px solid #eaeaea;
		.title{
			font-size:$f15;
			color:#4b4b4b;
			i{
				color:#838383;
				font-size:$f20;
			}
		}
		
		.detail{
			font-size:$f11;
			line-height: $f18;
			color:#5a5a5a;
			margin-top:$f15;
			p{
				text-indent: 2rem;
			}

			.pics{
				margin:$f12 0;
				img{
					width:100%;
				}
			}
		}
	}


	.big-events{
		ul{
			width:100%;
			height:auto;
			li{
				width:100%;
				display: block;
				height:3rem;
				line-height:3rem;
				span{
					display: inline-block;
				}
				.date{
					width:10%;
				}
				.node{
					width:10%;
					text-align: center;
					position:relative;
					.circle{
						display: inline-block;
						width:$f10;
						height:$f10;
						border-radius:50%;
						background-color:#bfbfbf;
						position:relative;
					}
					.slide{
						position:absolute;
						width:1px;
						height:100%;
						background-color:#bfbfbf;
						top:0;
						left:50%;
						margin-left:-1px;
					}
				}
				.event{
					width:75%;
					
				}
			}
			.year{
				color:$yellow;
				.node{
					.circle{
						background-color:$yellow;
					}
				}
			}
		}
	}
}

.security{
	.security-pics{
		width:200px;
		height:150px;
		background-image: url(#{$imgPath}/aboutBBL.png);
		background-size:400px 300px;
		background-repeat: no-repeat;
		margin:0 auto;
	}
	.tuoguan{
		background-position: 0 0;
	}

	.fengkong{
		background-position: -200px 0;
	}

	.yinsi{
		background-position: 0 -150px;
	}

	.law{
		background-position: -200px -150px;
	}


	ul {
		li{
			span{
				color:$red;
			}
		}
	}
}

.connection{
	span{
		color:$red;
	}
	.detail{
		padding:0 $f10;
	}
	.info{
		padding:$f10 0;
		.get-intouch-title{
			padding:0 $f10 $f10;
			border-bottom:1px solid #f2f2f2;
			color:$red;
			font-size:$f15;
			color:#4b4b4b;
			.iconfont{
				font-size:$f20;
				margin-right:$f10;
				color:$red;
			}
			.icon-shangwuhezuo{
				font-size:2.3rem;
			}
		}
	}
}

.latest-news{
	.info{
		padding:0;
	}
	li{
		color:#5a5a5a;
		position:relative;
		font-size:$f13;
		// line-height: $f15;
		border-bottom:1px solid #ebebeb;
		margin:$f20 $f15 0;
		padding-bottom:$f20;
		.words{
			width:75%;
			// height:100%;
			text-overflow: -o-ellipsis-lastline;
			overflow: hidden;
			text-overflow: ellipsis;
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
			padding-right:$f10;
			line-height: $f20;
		}
		.news-pic{
			width:25%;
			height:6rem;
			overflow: hidden;
			img{
				width:100%;
				height:100%;
			}
		}
		.date{
			font-size:$f12;
			position:absolute;
			bottom:$f18;
			left:0;
		}
	}
	li:last-child{
		border-bottom:none;
	}
}