.infolist{
	margin-top:41px;
	ul{
		li{
			border-bottom:1px solid #f1f1f1;
			padding:$f10 0 0.5rem;
			position:relative;
			line-height: $f18;
			padding-right:$f10;
			padding-left:0.5rem;
			.toNext{
				position:absolute;
				right:$f15;
				top:0;
				line-height: 4rem;
				font-size:$f20;
				color:#b7b6b6;
			}
			.info-content{
				position:relative;
				.info-title{
					font-size:$f13;
					color:#393939;
					line-height: $f20;
					height:$f20;
				}
				.info-date{
					font-size:$f11;
					color:#b7b6b6;
					line-height: $f20;
					height:$f20;
				}
			}
			.isNew::before{
				position:absolute;
				left:0.2rem;
				top:0.6rem;
				content: "";
				width:6px;
				height:6px;
				display: block;
				background-color:$red;
				border-radius:100%;
			}
		}
	}
	.no-Data{
		height:$f20;
		line-height: $f20;
		color:#c9c9c9;
		font-size:$f10;
		text-align:center;
	}
	
}