.notice{
	margin-top:40px;
	position:absolute;
	top:0;
	left:0;
	right:0;
	bottom:0;
	background-color:#eee;
	overflow-y:auto;
	li{
		padding:$f13 0;
		margin-bottom: 0.8rem;
		background-color:$white;
		.user-message,.project-remind{
			display: block;
			background-image: url(#{$imgPath}/info.png);
			width:4rem;
			height:4rem;
			background-size:8rem 4rem;
			background-repeat: no-repeat;
			background-position:0 0;
			vertical-align: top;
			margin-right: $f10;
		}
		.project-remind{
			background-position:-4rem 0;
		}

		.user-message::after,.project-remind::after{
			visibility: hidden;
			display: block;
			font-size:0;
			content:" ";
			clear:both;
			height:0;
		}

		.hasNew{
			display: block;
			position:relative;
		}
		.hasNew::before{
			position:absolute;
			width:8px;
			height:8px;
			border-radius:50%;
			top:-0.2rem;
			right:-0.2rem;
			content:"";
			background-color:$red;
		}
		.newMessage{
			.message-title{
				font-size:$f16;
				color:#393939;
				// margin-bottom: 0.2rem;
				line-height: 2.4rem;
				.title{
					width:70%;
					display: inline-block;
				}
				.date{
					font-size:$f12;
					color:#b7b6b6;
					line-height: 2.4rem;
				}
			}
			.abstract{
				font-size:$f12;
				line-height: $f16;
				color:#b7b6b6;
			}
			.message-pic{
				height: 125px;
				border-radius:5px;
				overflow: hidden;
				margin-bottom:$f10;
				margin-top:0.6rem;
				img{
					width:100%;
				}
			}
		}
	}
	.no-Data{
		height:$f20;
		line-height: $f20;
		color:#c9c9c9;
		font-size:$f10;
		text-align:center;
		// margin-bottom:$f20;
	}

	.projectsMessage,.myMessage{
		.newMessage{
			.abstract{
				font-size:$f13;
			}
		}
	}
}

