/* 底部导航 */
.footer{
	position:fixed;
	bottom:0;
	left:50%;
	-webkit-transform:translateX(-50%);
	-moz-transform:translateX(-50%);
	-ms-transform:translateX(-50%);
	-o-transform:translateX(-50%);
	transform:translateX(-50%);
	background-color:$white;
	height:50px;

	border-top:1px solid #f1f1f1;
	// padding:5px 0;

	.item{
		flex:1;
		text-align: center;
		color:#9d9d9d;
		.item-icon{
			height:34px;
			line-height: 34px;
			.iconfont{
				font-size:24px;
				color:#9d9d9d;
				font-weight: 100;
			}
			
		}
		.item-text{
			height:16px;
			line-height: 12px;
			font-size:$f10;
		}
		span {
			display: block;
		}
	}

	.active a{
		color:$red;
		.item-icon{
			
			.iconfont{
				color:$red;
			}

		}
	}

	.account{
		span{
			display: block;
			.item-icon{
				.iconfont{
					// font-size:$f20;
				}
			}
		}
		
	}


}

/* 头部导航 */
.header{
	background-color:$white;
	position:fixed;
	top:0;
	left:50%;
	-webkit-transform:translateX(-50%);
	-moz-transform:translateX(-50%);
	-ms-transform:translateX(-50%);
	-o-transform:translateX(-50%);
	transform:translateX(-50%);
	z-index:200;


	height:40px;
	line-height:40px;

	border-bottom:1px solid #f1f1f1;


	ul{
		width:100%;
		height:100%;
		li{
			display:block;
			float:left;
			height:100%;
			span{
				display: block;
				float: left;
				width:40px;
				height:100%;
				line-height:40px;
				i{
					font-size:$f14;
				}
			}
		}
		.back{
			width:25%;
			text-align:left;
			i{
				color:#797979;
			}
		}
		.title{
			width:50%;
			font-size:$f16;
			text-align:center;
		}
		.other{
			width:25%;
			span{
				float: right;
				text-align:right;
				i.setup{
					font-size:24px;
				}
			}
			span.all-read{
				color:$red;
				font-size:$f12;
				width:100%;
			}

			span.login{
				color:$red;
				font-size:$f13;
				width:100%;
			}
		}
	}

	
}

/* 用户输入 */
.user-inputs{
	display: inline-block;
	width:100%;
	height:auto;
	li{
		display: block;
		width:100%;
		height:60px;
		/* 输入框 */
		div.input-box{
			
			height:30px;
			border-bottom:1px solid #f1f1f1;
			label{
				width:30px;
				height:30px;
				display: block;
				line-height: 30px;
				text-align: center;
				float:left;
				margin-right: 0.5rem;
				.iconfont{
					font-size:$f18;
					color:$red;
					font-weight: 100;
				}
			}
			
			input{
				border:none;
				outline: none;
				width:85%;
				height:29px;
				font-size:$f14;
			}

		
		}
		/* 信息提示 */
		.tip-box{
			width:100%;
			height:24px;
			line-height: 24px;
			font-size:$f10;
			color:$red;
			text-align:left;
			padding-left:12%;
		}

		
	}
}


/* 选项卡 */
.tabs{
	position:fixed;
	top:40px;
	height:40px;
	line-height:40px;
	background-color:$white;
	// border-bottom:1px solid #eeeeee;
	box-shadow:0 1px #eee;
	z-index:100;
	left:50%;
	-webkit-transform:translateX(-50%);
	-moz-transform:translateX(-50%);
	-ms-transform:translateX(-50%);
	-o-transform:translateX(-50%);
	transform:translateX(-50%);

	overflow-x: auto;
	.tab-option{
		-webkit-flex:1;
		flex:1;
		font-size:$f14;
		color:#5a5a5a;
		text-align: center;

		span{
			display:inline-block;
			height:40px;
			// padding:0 12px;
			// margin-bottom: 2px;
		}
	}
	.select{
		color:$red;
		span{
			border-bottom:1px solid $red;
			padding-left:$f10;
			padding-right:$f10;
		}
	}
}


/* 帮助中心选项卡 */
.tabs.help-center-tabs{
	height:auto;
	padding-top:$f10;
	padding-bottom:$f10;
	border-bottom:2px solid #dbdbdb;
	span{
		width:6rem;
		height:6rem;
		background-image: url(#{$imgPath}/help-center.png);
		background-size:24rem 6rem;
		background-repeat:no-repeat;
		
		border:none;
		margin-bottom:0;
		display: block;
		margin:0 auto;
	}
	.tab-option{
		opacity: 0.5;
	}
	.select{
		color:#2d2d2d;
	}
	.select{
		opacity: 1;
	}

	.zcdl span{
		background-position:0 0;
	}
	.cztx span{
		background-position:-6rem 0;
	}
	.tzlc span{
		background-position:-12rem 0;
	}
	.bfj span{
		background-position:-18rem 0;
	}
}
.bblIntro{
	overflow-x:auto;
	.tab-option{
		// font-size:$f13;
		span{
			display: inline-block;
			// padding:0;
		}
		// padding: 0 $f10;
		min-width:8rem;
	}
}







/* 提示框 */
.prompt-information{
	font-size: $f12;
	width: 100%;
	padding: 0.5rem $f10;
	-webkit-transition: all 1s;
	-moz-transition: all 1s;
	-ms-transition: all 1s;
	-o-transition: all 1s;
	transition: all 1s;
	top: 0;
	left: 0;
	// -webkit-transform: translate(0,-100%);
	// -moz-transform: translate(0,-100%);
	// -ms-transform: translate(0,-100%);
	// -o-transform: translate(0,-100%);
	// transform: translate(0,-100%);
	position: fixed;
	color: #fff;
	z-index:9999;
	background-color: rgba(231,36,24,0.6);
	.prompt-content{
		width: 100%;
		i.iconfont{
			margin-right:0.5rem;
			display: inline-block;
			// color:#000;
			vertical-align: middle;
			font-size: $f15;
		}
	}
}


/* 错误信息提示框 */
.tipsBox{
	position:fixed;
	top:50%;
	left:50%;
	max-width:320px;
	width:60%;
	-webkit-transform:translate(-50%,-50%);
	-moz-transform:translate(-50%,-50%);
	-ms-transform:translate(-50%,-50%);
	-o-transform:translate(-50%,-50%);
	transform:translate(-50%,-50%);
	z-index:100;
	color:$white;
	font-size:$f12;

	-webkit-transition:all 1s;
	-moz-transition:all 1s;
	-ms-transition:all 1s;
	-o-transition:all 1s;
	transition:all 1s;
	.tipsBox-content{
		background-color:rgba(0,0,0,0.6);
		border-radius:0.5rem;
		padding-top:$f10;
		padding-bottom:$f10;
		// height:8rem;
	}
}


/* 分享 */
.share-box{
	position:fixed;
	z-index:400;
	background-color:rgba(0,0,0,0.5);
	width:100%;
	height:100%;
	top:0;
	left:50%;

	-webkit-transform:translateX(-50%);
	-moz-transform:translateX(-50%);
	-ms-transform:translateX(-50%);
	-o-transform:translateX(-50%);
	transform:translateX(-50%);

	.qrCode{
		position:absolute;
		top:50%;
		left:50%;
		-webkit-transform:translate(-50%,-50%);
		-moz-transform:translate(-50%,-50%);
		-ms-transform:translate(-50%,-50%);
		-o-transform:translate(-50%,-50%);
		transform:translate(-50%,-50%);
		img{
			display: block;
			margin:0 auto;
			margin-bottom:10%;
			max-width:100%;
		}
	}

	.share-content{
		position:absolute;
		bottom:-500px;
		left:0;
		width:100%;
	}

	.share-methods{
		background-color:#dedede;
		padding-top:$f15;
		padding-bottom:0;
		.title{
			margin-bottom:$f10;
			font-size: $f12;
			color:#333333;
		}
		ul{
			width:100%;
			li{
				border-bottom:1px solid #c0c0c0;
				height:90px;
				padding:10px;
				a{
					display: block;
					float: left;
					width:20%;
					text-align: center;
					font-size: $f12;
					color:#333;
					span{
						display: block;
						margin:0 auto 0.5rem;
						width:50px;
						height:50px;
						background-image: url(#{$imgPath}/share-logos.png);
						background-size:350px 50px;
						background-repeat:no-repeat;
					}

					.penyouquan{
						background-position: -50px 0;
					}
					.qq{
						background-position: -100px 0;
					}
					.kongjian{
						background-position: -150px 0;
					}
					.weibo{
						background-position: -200px 0;
					}
					.links{
						background-position: -250px 0;
					}
					.saomiao{
						background-position: -300px 0;
					}
				}
			}
			li:nth-child(2){
				border-bottom:none;
			}
		}
	}


	a.cancel-share{
		border-radius:0;
		color:#333333;
		background-color:#f7f7f7;
	}
}

