.freshmen-experience{
	margin-top:40px;
	position:absolute;
	top:0;
	left:0;
	right:0;
	bottom:45px;
	background-color:#f8f8f8;
	overflow-y:auto;
	ul {
		li{
			display: block;
			background-color:$white;

			padding:0 18px;
			width:100%;
			max-width:640px;
			margin:0 auto;

			span:nth-child(1){
				font-size:$f13;
				color:#2d2d2d;
				width:30%;
				display: inline-block;
			}
			span:nth-child(2){
				font-size:$f12;
				color:#666666;
			}
			height:4rem;
			line-height: 4rem;
			border-bottom: 1px solid #f1f1f1;

			.assets-value{
				padding:$f10 0;
				line-height:3rem;
				.value1{
					font-size: $f12;
					display: inline-block;
					padding:0 $f10;
					background-color: #ccc;
					color:#fff;
					border-radius:5px;
					margin-right:1rem;
				}
				.value1.select{
					background-color:$red;
					border:none;
					color:$white;
				}
			}
		}
		li.choose-value{
			margin:0.8rem 0;
			height:auto;
		}
	}
}


.confirm-experience{
	position:fixed;
	bottom:0;
	border-radius:0;
	left:50%;


	-webkit-transform:translateX(-50%);
	-moz-transform:translateX(-50%);
	-ms-transform:translateX(-50%);
	-o-transform:translateX(-50%);
	transform:translateX(-50%);
}