.invest-filter{
	position:fixed;
	top:40px; 
	left:50%;
	-webkit-transform:translateX(-50%);
	-moz-transform:translateX(-50%);
	-ms-transform:translateX(-50%);
	-o-transform:translateX(-50%);
	transform:translateX(-50%);
	z-index:100;

	.filter-condition{
		.filter{
			-webkit-flex:1;
			flex:1;
			text-align: center;
			height:40px;
			line-height: 40px;
			color:$filter-color;
			font-size:$f14;
			background-color: $white;
		}
	}
	.filter-list{
		position:absolute;
		width:100%;
		height:auto;
		background-color:$white;
		ul li{
			display: block;
			width:100%;
			text-align: center;
			color:$filterList-font-color;
			font-size:$f12;
			padding:5px 0;
		}
		.selected{
			color:$red;
			position:relative;
			.tick{
				position:absolute;
				right:10%;
			}
		}
	}
}

#investList{
	background-color:$filter-bgcolor;
	margin-top:80px;
	position:relative;
	padding-top:10px;
	background-color:#f8f8f8;


	.list{
		margin-bottom:50px;
		li{
			background-color:$white;
			position:relative;
			margin-bottom:$f10;
			.progress{
				position:absolute;
				left:0;
				top:0;
				width:50%;
				height:100%;
				position:absolute;
				background-color:#fefbeb;
			}
			.list-item{
				position:relative;
				color:$filter-listItem-color;
				.title{
					display: relative;
					font-size:$f12;
					padding-top:$f12;
					padding-bottom:$f12;
					border-bottom:1px solid $list-bottom-color;
				}
				.detail{
					text-align: center;
					border-bottom:1px solid $list-bottom-color;
					padding-top:$f12;
					padding-bottom:$f12;
					div:nth-child(2){
						font-size:$f11;
					}
					.rate{
						-webkit-box-flex: 1;   /* OLD - iOS 6-, Safari 3.1-6 */  
					    -ms-flex: 1;           /* IE 10 */  
						-webkit-flex:1;
					    -moz-box-flex: 1;       /* OLD - Firefox 19- */    
						flex:1;
						width:25%;
						
						.rate-num{
							font-size:$f14;
							line-height: $f20;
							color:$red;
							margin-bottom:0.5rem;
							span{
								font-size:$f10;
								vertical-align: top;
								line-height: $f10;
							}
						}
					}
					.duration{
						-webkit-box-flex: 1;   /* OLD - iOS 6-, Safari 3.1-6 */  
					    -ms-flex: 1;           /* IE 10 */  
						-webkit-flex:1;
					    -moz-box-flex: 1;       /* OLD - Firefox 19- */    
						flex:1;

						width:25%;
						.days{
							margin-bottom:0.5rem;
							font-size:$f12;
							line-height: $f20;
							height:$f20;
						}
					}
					.money{
						-webkit-box-flex: 1;   /* OLD - iOS 6-, Safari 3.1-6 */  
					    -ms-flex: 1;           /* IE 10 */  
						-webkit-flex:1;
					    -moz-box-flex: 1;       /* OLD - Firefox 19- */    
						flex:1;

						width:25%;
						border:none;
						.total{
							margin-bottom:0.5rem;
							font-size:$f13;
							line-height: $f20;
						}
					}
					.state{
						-webkit-box-flex: 1;   /* OLD - iOS 6-, Safari 3.1-6 */  
					    -ms-flex: 1;           /* IE 10 */  
						-webkit-flex:1;
					    -moz-box-flex: 1;       /* OLD - Firefox 19- */    
						flex:1;

						width:25%;
						.btn{
							width:100%;
							height:2.5rem;
							font-size:$f12;
							line-height: 2.5rem;

							border-radius:$f20 0 0 $f20;
							text-align: left;
							padding-left:$f10;
						}
						.state-getReady{
							background-color: $yellow;
						}
						.state-paying{
							background-color:$white;
							color:$black;
						}
						.state-finish,.state-fail,.state-failtosold,.state-overdue{
							background-color:#979797;
						}
						.state-full{
							background-color:rgba(231,36,24,0.7);
						}
					}
				}
				.item-footer{
					padding-top:0.8rem;
					padding-bottom:0.8rem;
					font-size:$f10;
					span{
						margin-right:0.5rem;
						display:inline-block;
						line-height: $f20;
						height: $f20;
						i{
							font-size: $f12;
							color:$yellow;
							line-height: $f20;
							margin-right:0.2rem;
						}
					}
				}

				
			}
			
		}
	}
}