@charset "utf-8";
 
html{
	font-size:11px;
}
@media (max-height:666px){
	html{
		font-size:11px;
	}
}
@media (max-height:568px){
	html{
		font-size:10px;
	}
}


 html *{
	padding:0;
	margin:0;
	box-sizing:border-box;
	font-family:"SimHei";
}


body{
	// background-color:#eee;
}

a{
	text-decoration:none;
	color:$black;
}


ul,ol{
	list-style:none;
}

.fr{
	float:right;
}

.fl{
	float:left;
}


/* 清除浮动 */
.clear:after{
	clear: both;
	zoom: 1;
	display:block;
	content: '.';
	visibility:hidden;
	height:0;
}

/* 设置宽 */
.full-container{
	width:100%;
	max-width:640px;
	margin:0 auto;
}

/* 设置宽，两边间隙18px */
.container{
	padding:0 18px;
	width:100%;
	max-width:640px;
	margin:0 auto;
}

/* iphone5 以下边距调整为10 */
@media (max-width:360px){
	.container{
		padding:0 12px;
		width:100%;
		max-width:640px;
		margin:0 auto;
	}
}


.hide{
	display:none;
}

/* flex布局 */
.flex{
	display: -webkit-flex;  /* 新版本语法: Chrome 21+ */
    display: -webkit-box;   /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. */
    display: -moz-box;      /* 老版本语法: Firefox (buggy) */
    display: -ms-flexbox;   /* 混合版本语法: IE 10 */   
    display: flex;          /* 新版本语法: Opera 12.1, Firefox 22+ */
}

.flex-middle{
	display:-webkit-flex;
	display:-ms-flexbox;
	display:flex;

	align-items:center;
}

/* 下一步按钮 */
span.toNext{
	float:right;
	min-width:40px;
	text-align: right;
	.iconfont{
		font-size:$f12;
	}
}


/* 多余文字处理 */
.surplus{
	overflow: hidden;  
   	white-space: nowrap;  
   	text-overflow: ellipsis; 
}

.h50{
	height:50%;
}
.Pcenter{
	width:100%;
	text-align:center;
}

