.footer-nav{
	position:fixed;
	bottom:0;
	left:0;
	border-top:1px solid #000;
	width:100%;
	height:auto;
	background-color:#58a;
}
.btn[disabled]{
	background-color:#e95f57;
}
.btn{
	display:block;
	border-radius:5px;
	background-color:$red;
	color:$white;
	height: 45px;
	line-height: 45px;
	font-size:18px;
	text-align:center;
	outline:none;
	border:none;
	margin:0 auto;
	width:100%;
}



.btn.inactive{
	background-color:$gray;
}

.btn.signout{
	background-color:$white;
	color:$red;
	position:fixed;
	bottom:0;

}


.red{
	color:$red;
}

