.lottery-box{
	position:absolute;
	top:0;
	left:0;
	right:0;
	bottom:0;
	height:100%;
	background-image:url(#{$imgPath}/personal-bg.gif);
	background-size:cover;
	background-repeat:no-repeat;
	background-position:center center;

	.bbl-logo{
		width:70px;
		height:21px;
		background-image:url(#{$imgPath}/BBL-logo2.png);
		background-size:100% 100%;
		background-repeat:no-repeat;
		margin-top: 12px;
		margin-left:15px;
	}

	

	.lottery-content{
		position:absolute;
		top:50%;
		left:0;
		-webkit-transform: translateY(-50%);
		-moz-transform: translateY(-50%);
		-ms-transform: translateY(-50%);
		-o-transform: translateY(-50%);
		transform: translateY(-50%);
		.summary{
			font-size:$f13;
			color:$white;
			text-align: center;
			margin-bottom:$f15;
		}
		.summary.big{
			font-size: 2.5rem;
			color:#fbf200;
			margin-bottom:$f20;
		}

		.lottery-card{
			width:270px;
			height:120px;
			margin:0 auto;
			background-image:url(#{$imgPath}/lottery-card.png);
			background-size:100% 100%;
			padding:10px;
			.card {
				width:100%;
				height:100%;
				position: relative;
				text-align: center;
				background-color:#ffc603;
				text-align: center;
				p{
					font-size: $f15;
					color:#693b04;
					line-height: 30px;
				}
				.logs{
					text-decoration: underline;
					color:#ff2124;
					font-size: $f12;
					line-height: 30px;
				}
				.readed{
					color:$white;
					width:60%;
					height:35px;
					line-height: 35px;
				}

			}
			canvas{
				position: absolute;
				top: 0;
				left: 0;
				width:250px;
				height:100px;
			}

			.front{
				width:100%;
				height:100%;
				background-color:#ffc603;
				font-size: $f15;
				line-height: 50px;
				color:#693b04;
				text-align: center;
				.btn{
					background-color:#ff2124;
					bottom:0;
					width:60%;
					font-size: $f15;
					height:35px;
					line-height: 35px;
				}
			}
		}
	}
}