.ivest-logs{
	margin-top:40px;
	.detail-title{
		height:40px;
		line-height: 40px;
		font-size: $f13;
		border-bottom:1px solid #ececec;
		div{
			-webkit-flex:1;
			flex:1;
			text-align: center;
		}
	}

	.logs{
		ul{
			width:100%;
			height:auto;
			li{
				padding-top:$f10;
				padding-bottom:$f10;
				border-bottom:1px solid #ececec;
				div{
					-webkit-flex:1;
							flex:1;
					text-align: center;
					font-size:$f12;
					line-height: 30px;
				}

				div:nth-child(3) p{
					line-height: $f14;
					display: block;
				}

			}
		}
	}
}