.my-bank{
	margin-top:40px;


	
	.bankbox{
		height:auto;
		padding-top:$f20; 
		padding-bottom:$f20; 
		background-color: $red;
		.banklogo{
			padding:0.5rem;
			background-color:$white;
			display: inline-block;
			border-radius:50%;
			margin-right:$f10;
			.bank{
				margin:0;
			}
		}
		.bankname,.banknum{
			font-size: $f12;
			color:$white;
			line-height: $f16;
			
		}


	}

	// .boc,.icbc,.citic,.gdb,.hxb,.szpab,.cmb{
	// 	background-color: $red;
	// }


	.unbind{
		margin-top:4rem;
	}
}