.popup{
	position:fixed;
	top:0;
	left:50%;
	
	height:100%;

	-webkit-transform:translateX(-50%);
	-moz-transform:translateX(-50%);
	-ms-transform:translateX(-50%);
	-o-transform:translateX(-50%);
	transform:translateX(-50%);

	background-color:rgba(0,0,0,0.5);
	z-index:300;


	.popup-content{
		// margin:$f10;

		width:80%;
		height:auto;
		overflow:hidden;

		top:50%;
		left:50%;
		position:absolute;

		-webkit-transform:translate(-50%,-50%);
		-moz-transform:translate(-50%,-50%);
		-ms-transform:translate(-50%,-50%);
		-o-transform:translate(-50%,-50%);
		transform:translate(-50%,-50%);
		background-color:$white;
		border-radius:5px;
		height:auto;

		/* 按钮组 */
		.popup-btns{
			position:fixed;

			bottom:0;
			width:100%;
			
			overflow:hidden;
			border-radius:0 0 5px 5px;

			
			.btn{
				border-radius:0;
				-webkit-flex:1;
				flex:1;

				background-color:$white;
				color:$red;
				border-right:1px solid #e6e6e6;
				border-top:1px solid #e6e6e6;

				height:4rem;
				line-height: 4rem;
				font-size:$f16;
			}
			.btn:last-child{
				border-right:none;
			}

		}

		/* 弹框内容 */
		.detail{
			padding-top:4rem;
			padding-bottom:7rem;;

			max-height:400px;
			overflow-y:auto;

			font-size: $f18;
			line-height: $f20;
			color:$black;
			text-align: center;

			img{
				max-width: 20%;
				margin:$f10 0;
			}
		}
	}
}