.homepage{
	background-color:#f8f8f8;
	margin-bottom:50px;
	.banner{
		font-size: 0px;
		img{
			width:100%;
		}
	}

	/* 累计交易额 */
	.trade-volume{
		padding-top:$f10;
		padding-bottom:$f10;
		background-color: $white;
		border-bottom:1px solid #efefef;
		margin-bottom:$f10;

		.go-detail{
			font-size:$f11;
			color:$red;
			.toNext{
				font-size:$f11;
				color:#666666;
			}
			margin-bottom:$f10;
		}

		.total-num{
			font-size:$f12;
			color:#666666;
			span{
				font-size:$f16;
				padding:0.1rem 0.2rem;
				border:1px solid #d3d3d3;
				border-radius:3px;
				margin-right:3px;
			}
		}
	}


	/* 推荐项目 */
	.recommend{
		border-top:1px solid #efefef;
		border-bottom:1px solid #efefef;
		background-color:$white;
		margin-bottom: $f10;

		.invest-name{
			font-size: $f14;
			color:#666;
			padding-top:$f10;
			padding-bottom:0.5rem;
			border-bottom:1px solid #f5f5f5;
			line-height: $f16;
			span{
				font-size:$f10;
				vertical-align: middle;
			}
			i{
				color:$yellow;
				margin-right:0.2rem;
			}
		}

		.invest-intro{
			width:100%;
			.rate{
				-webkit-flex:1;
						flex:1;
				padding:$f20 0;
				.rate-num{
					margin-bottom:$f15;
					p.expect{
						height:$f18;
						line-height: $f18;
						font-size:$f18;
						color:$red;
						span{
							font-size:$f12;
							display: inline-block;
							vertical-align: top;
							margin-top: -0.5rem;
						}
					}
				}
				.intro-title{
					font-size: $f12;
					color:#666;
				}
			}
			.days{
				-webkit-flex:2;
						flex:2;
				padding:$f20 0;
				.duration{
					height:$f18;
					line-height: $f18;
					font-size:$f14;
					color:#333;
					margin-bottom:$f15;
				}
				.days-title{
					font-size: $f12;
					color:#666;
				}
			}
			.buy{
				-webkit-flex:2;
						flex:2;
				padding:$f20 0;
				.invest-btn{
					background-color: $orange;
					width:90%;
					height:40px;
					line-height: 40px;
				}
			}
		}
	}


	/*  四大功能  */
	.four-features{
		margin-bottom: $f10;
		background-color: $white;
		.feature{
			text-align: center;
			width:100%;
			i{
				display: block;
				font-size:2.5rem;
				color:$red;
				margin-bottom:0.7rem;
			}

			.title{
				display:block;
				font-size:$f14;
				color:#4d4d4d;
				margin-bottom:0.3rem;
			}
			.detail{
				font-size:$f10;
				color:#ababab;
				display:block;
			}

			.feature-cont{
				width:50%;
				padding:$f15 0;
				-webkit-flex:1;
				flex:1;
			}
			.feature-cont:nth-child(1){
				border-right:1px solid #f3f3f3;
				margin-left:-1px;
			}
			.BBL-logo i{
				margin:0 auto;
				width:3.2rem;
				height:2.5rem;
				background-image:url(#{$imgPath}/bbl.png);
				background-size:100% 100%;
				margin-bottom:1rem;
			}
		}
		.feature:nth-child(1){
			border-bottom:1px solid #f3f3f3;
		}
		
	}



	/* 平台公告 */
	.notice-news{
		background-color: $white;
		font-size:$f13;
		color:#4d4d4d;
		margin-bottom: $f10;
		padding-top:0.5rem;
		padding-bottom:0.5rem;

		.notice-logo{
			display: block;
			float: left;
			width:3.7rem;
			height:3.6rem;
			background-image: url(#{$imgPath}/notice.png);
			background-size:100% 100%;
			margin-right: 10px;
		}
	}

	.big-events{
		font-size: 0;
		img{
			width:100%;
		}
	}







}