.header ul li span .icon-shezhi{
	color:$orange;
	font-size:$f16;
}

.user-account{
	padding-top:40px;
	height:auto;
	background-color:$account-bgcolor;
	.myData{
		position:relative;
		height:auto;
		img{
			width:100%;
			font-size:0px;
			display:block;
		}
		.account{
			position:absolute;
			width: 100%;
			height:100%;
			top:0;
			left:0;
			ul{
				width:100%;
				height:100%;
				padding-top: $f10;
				li{
					width:100%;
					color:$white;
					text-align: center;
				}
				.accumulate{
					font-size:$f12;
					height:20%;
				}
				.all-income{
					font-size:2.5rem;
					height:25%;
				}
				.pig{
					font-size:$f13;
					height:30%;
				}
				.total{
					font-size:$f12;
					// line-height: $f20;
					.all-assets{
						width:50%;
						text-align: center;
						a{
							color:$white;
							display: inline-block;
							border-bottom: 1px solid $white; 
							margin-top: -1px;
						}
					}
					.all-money{
						width:50%;
						text-align: center;
					}
					span{
						display: block;
						font-size:$f16;
						margin-top:0.4rem;
					}
				}
			}
		}
	}
	.money-operate{
		height:41px;
		.btn{
			width:50%;
			background-color: $white;
			color:$red;
			font-size:$f15;
			border-radius: 0;
			border-bottom:1px solid #f1f1f1;
			border-right:1px solid #f1f1f1;
		}
		.tixian{
			border-right:none;
		}
		margin-bottom:10px;
	}

	.functions{
		border-top:1px solid #f1f1f1;
		border-bottom:1px solid #f1f1f1;
		.wrap{
			width:100%;
		}
		.item{
			width:33.33%;
			padding:10px 0;
			-webkit-flex:1;
			flex:1;
			text-align: center;
			font-size:$f14;
			color:#494949;
			border-right:1px solid #f1f1f1;
			border-top:1px solid #f1f1f1;
			background-color:$white;
			i{
				font-weight: 100;
				display: block;
				font-size:2.5rem;
				line-height: 3.5rem;
				color:$red;
				margin-bottom:5px;
			}
			.icon-youjian{
				font-size: 2.2rem;
			}
			a{
				color:$functions-font-color;
			}
		}
		.wrap:nth-child(1){
			.item{
				border-top:none;
			}
		}
		.item:nth-child(3){
			border-right:none;
		}
	}
	
}