.operation-success{
	position:absolute;
	top:40%;
	left:50%;
	-webkit-transform:translate(-50%,-50%);
	-moz-transform:translate(-50%,-50%);
	-ms-transform:translate(-50%,-50%);
	-o-transform:translate(-50%,-50%);
	transform:translate(-50%,-50%);

	font-size:$f16;
	color:#767777;
	text-align:center;

	img{
		width:15%;
		margin-bottom:3rem;
	}	

	.back-home{
		margin:0 auto;
		
		margin-top:8rem;
		.btn{
			font-size: $f20;
		}
	}
}