.wallet{
	margin-top:40px;
	background-color:$white;
	padding-top:$f10;
	.chart{
		width:200px;
		height:200px;
		margin:0 auto $f10;

		canvas{
			margin:0 auto;
			display: block;
		}
		position:relative;
		.chart-intro{
			position:absolute;
			top:50%;
			left:50%;
			width:140px;
			height:140px;
			-webkit-transform: translate(-50%,-50%);
			-moz-transform: translate(-50%,-50%);
			-ms-transform: translate(-50%,-50%);
			-o-transform: translate(-50%,-50%);
			transform: translate(-50%,-50%);
			background-color:$white;
			border-radius:50%;
			.intro{
				position:absolute;
				width:100%;
				text-align: center;
				top:50%;
				left:50%;
				-webkit-transform: translate(-50%,-50%);
				-moz-transform: translate(-50%,-50%);
				-ms-transform: translate(-50%,-50%);
				-o-transform: translate(-50%,-50%);
				transform: translate(-50%,-50%);
				font-size:$f12;
				color:#2d2d2d;
				.all-money{
					display: block;
					font-size:$f15;
					color:#000000;
					margin-top:$f10;
				}
			}
		}
	}
	.item-list{
		margin-bottom:$f15;
		ul{
			width:100%;
			height:auto;

			li{
				display: block;
				padding:$f15 $f10 0 2rem;
				font-size:$f12;
				color:#2d2d2d;
				border-bottom:1px solid #f1f1f1;
				.chart-item{
					
					padding-bottom:$f15;
					span{
						color:#000;
					}
				}

				.chart-item::before{
					content: " ";
					display: block;
					width:0.8rem;
					height:0.8rem;
					border-radius:50%;
					position:absolute;
					left:0.5rem;
					
					top:$f17;
				}
				.last::before{
					background-color:$red;
				}
				.capital::before{
					background-color:$orange;
				}
				.interest::before{
					background-color:$yellow;
				}
				.invest-money::before{
					background-color:#dbb866;
				}
				position:relative;
				
			}
		}
	}
	.sum{

		.nums{
			-webkit-flex:1;
			flex:1;
			padding:$f10 0;
			text-align: center;
			font-size:$f12;
			color:#2d2d2d;
			span{
				display: inline-block;
				font-size:$f14;
				color:$red;
				margin:$f10;
			}
		}
	}
}