.transaction-details{
	margin-top:80px;
	.list{
		ul li{
			background-color: $white;
			border-bottom:1px solid #d6d6d6;
			padding:$f10 0;
			
			color:#717171;
			.icon-zhengque{
				color:$yellow;
				font-size:$f14;
			}
			.icon-cuowu{
				color:#7d7d7d;
				font-size:$f14;
			}
			.icon-bangzhu{
				color:$yellow;
				font-size:$f14;
			}
			.detail{
				margin-left:$f18;
			}
			.card{
				margin-bottom:0.8rem;
				font-size:$f13;
				i{
					font-size:0.9rem;
				}
				.detail-title{
					width:65%;
					display: inline-block;
				}

				.fr{
					font-size:$f11;
					line-height: $f18;
				}
			}
			.date{
				font-size:$f10;
				span{
					font-size:$f10;
				}
			}
		}

	}
}