.assets-planner-header{
	background-image:url(#{$imgPath}/assets-planner.png);
	background-size:100% auto;
	border-bottom:none;
	color:$white;
	.other{
		.rules{
			color:$white;
			text-decoration: underline;
		}
	}
	.back{
		span{
			i{
				color:$white;
			}
		}
	}
}
.assets-planner{
	.levels{
		background-image:url(#{$imgPath}/assets-planner.png);
		background-size:100% auto;
		padding-top:40px;
		padding-bottom:$f15;
		ul{
			li{
				padding-top:$f10;
				padding-bottom:$f10;
				.level-icon{
					-webkit-flex:2;
							flex:2;
					span{
						display: inline-block;
						width:3rem;
						height:3rem;
						background-image:url(#{$imgPath}/levels.png);
						background-size:100% 100%;
						position:relative;
						i.level-num{
							position:absolute;
							font-size:$f15;
							color:$white;
							right:0.4rem;
							bottom:0.2rem;
							font-style:normal;
						}
					}
				}
				.level-explain{
					-webkit-flex:8;
							flex:8;
					p{
						line-height: $f15;
						height: $f15;
						color:$white;
						font-size:$f10;
					}
					display: inline-block;
				}

				.isPrivilege{
					-webkit-flex:2;
							flex:2;

					span{
						display: inline-block;
						width:68px;
						height:20px;
						background-image:url(#{$imgPath}/privilege.png);
						background-size:100% 100%;
					}
				}
			}

			li:nth-child(4){
				.level-explain p{
					line-height: 3rem;
				}
			}
		}
		.be-planner{
			background-color:#fcc800;
			width:45%;
			margin-top:$f15;
			// margin-bottom:$f10;
			height:35px;
			line-height: 35px;
		}
	}

	.invite-log{
		border-bottom:1px solid #e6e6e6;
		.friends,.commission,.total-invest{
			padding:$f10 0;
			-webkit-flex:1;
					flex:1;
			span{
				display: block;
				text-align: center;
				width:100%;
				font-size:$f11;
				color:#666;
			}
			span:nth-child(2){
				margin-top:$f10;
				color:#333333;
			}
		}
		.friends,.commission{
			border-right:1px solid #e6e6e6;
		}
		.friends{
			.new-friends{
				width:6px;
				height:6px;
				border-radius:50%;
				background-color:$red;
				display: inline-block;
				vertical-align: top;
				margin-top:-3px;
			}
		}
	}

	.month-commission{
		padding-top:2.5rem;
		font-size: $f12;
		color:#666;
		text-align: center;
		span{
			color:$red;
		}
		p.earn-money{
			margin:0.5rem 0 $f10;
			color:$red;
			span{
				font-size: 2.5rem;
			}
		}

	}

	.add-friends{
		margin-top:$f20; 
		margin-bottom:$f12; 
		a{
			font-size: $f14;
		}
	}
}


.assets-planner.alreadybe{
	.levels{
		padding-top:50px;
		padding-bottom:$f20;
	}
	li{
		padding-top:$f15;
		padding-bottom:$f15;
	}
	li.complete{
		.level-icon{
			span{
				background-image:url(#{$imgPath}/levels-reach.png);
			}
		}
	}
	.add-friends{
		margin-top:4rem; 
	}
}

.assets-planner-rules{
	padding-top:40px;
	img{
		width:100%
	}
}


.assets-planner-apply{
	margin-top:40px;
	.must-complete{
		padding-top:$f15;
		p {
			color:$red;
			text-align: center;
			font-size: $f14;
			margin-bottom:$f15;
		}
		ul {
			li {
				padding-left:2rem;
				font-size: $f13;
				color:#505050;
				line-height: $f20;
				position:relative;
				span{
					position:absolute;
					left:0;
					top:0.3rem;
					width:$f14;
					height:$f14;
					text-align: center;
					border-radius:50%;
					line-height: $f16;
					font-size: $f10;
					display: inline-block;
					background-color:$red;
					color:$white;

				}
			}
		}
	}

	.btns{
		margin-top: $f20;
		div{
			-webkit-flex:1;
					flex:1;
			a.btn{
				background-color:$orange;
			}
		}
	}
}


.assets-planner-commission{
	margin-top:40px;

	/* 暂无佣金信息 */
	.no-commission{
		margin-top: 50%;
		text-align: center;
		span{
			margin:0 auto;
			width:90px;
			height:80px;
			background-image:url(#{$imgPath}/no-commission.png);
			background-size:100% 100%;
			display: block;
			margin-bottom:$f20;
		}
		font-size: $f20;
		color:#e2e2e2;
	}

	.commission-log{
		.month{
			background-color:#f8f8f8;
			padding-top:0.5rem;
			padding-bottom:0.5rem;
			font-size: $f14;
			color:#666;
		}
		.month-list{
			.list-title{
				padding-top:$f10;
				padding-bottom:0.5rem;
				border-bottom:1px solid #e6e6e6;
				div{
					-webkit-flex:1;
							flex:1;
					
					font-size:$f14;
					color:#505050;
					span{
						font-size:$f10;
					}
					text-align: center;
				}
				.total-money{
					padding-left:$f20;
				}
			}
			ul{
				
				li.list-item{
					
					border-bottom:1px solid #e6e6e6;
					padding-right:$f20;
					.person,.invest{
						padding-top:0.5rem;
						padding-bottom:0.5rem;
					}
					.person{
						font-size:$f13;
						color:#505050;
					}
					.invest{
						font-size:$f12;
						color:#999;
					}
					
					.list-item-detail{
						position:relative;
						padding-left:2.5rem;
						i.icon-zhengque{
							position:absolute;
							left:0;
							font-size:$f13;
							color:$yellow;
							top:0.5rem;
						}
					}
				}
			}
		}
	}
}


.assets-planner-friends{
	margin-top:80px;
	.friends-list-title{
		height:4rem;
		line-height: 4rem;
		border-bottom:1px solid #e6e6e6;
		font-size:$f13;
		color:#333;
	}

	li{
		height:4rem;
		line-height: 4rem;
		border-bottom:1px solid #e6e6e6;
		font-size:$f13;
		color:#333;
		.is-remind{
			a.reminds{
				display: inline-block;
				height:2rem;
				line-height: 2rem;
				font-size: $f11;
				background-color:$yellow;
				width:auto;
				padding:0 0.5rem;
			}
			a.invalid{
				background-color:#cccccc;
			}
		}
	}
	.name{
		-webkit-flex:2;
				flex:2;
		text-align: center;
	}
	.phonenum{
		-webkit-flex:3;
				flex:3;
		text-align: center;
	}
	.is-remind{
		text-align: center;
		-webkit-flex:2;
				flex:2;
	}
}