.u-detail{
	position:absolute;
	top:0;
	left:0;
	right:0;
	bottom:0;
	.swiper-container{
		width:100%;
		height:100%;
		.swiper-wrapper{
			width:100%;
			height:100%;
			.swiper-slide{
				font-size:$f14;
				color:$white;
				width:100%;
				height:100%;
				background-image:url(#{$imgPath}/personal-bg.gif);
				background-size:cover;
				background-repeat:no-repeat;
				background-position:center center;
				.bbl-logo{
					width:70px;
					height:21px;
					background-image:url(#{$imgPath}/BBL-logo2.png);
					background-size:100% 100%;
					background-repeat:no-repeat;
					margin-top: 12px;
					margin-left:15px;
				}
				.detail-content{
					width:100%;
					text-align: center;
					p{
						margin-bottom:0.5rem;
					}
					.join{
						.join-date,.total-days span{
							font-size:2.4rem;
							color:#fbf200;
						}
						
						p:nth-child(2){
							margin-bottom: 3.5rem;
						}
					}

					.lottery{
						.change-date,.lottery-nums{
							font-size:2.4rem;
							color:#fbf200;
						}
						p:nth-child(2){
							margin-bottom: 3.5rem;
						}
						p:nth-child(4){
							margin-bottom: 5rem;
						}
						.jump{
							display: inline-block;
							margin-bottom:2.5rem;
							color:#fbf200;
							border-bottom:1px solid #fbf200;
							line-height: $f20;
							font-size: $f15;
						}
					}


					.total-invest{
						.invest-total-money,.income-total-money,.overcome{
							font-size:2.4rem;
							color:#fbf200;
						}
						img{
							width:62%;
							margin-top:7rem;
							margin-bottom:5rem;
						}
					}

					.first{
						span{
							color:#fbf200;
						}
						span.all-days{
							font-size:2.4rem;
							color:#fbf200;
						}
						img{
							width:48%;
							margin-top:5rem;
							margin-bottom:3.5rem;
						}
					}

					.invest-history{
						img{
							width:30%;
						}
						
						.item{
							text-align: left;
							p{
								margin-bottom: 0;
								span{
									font-size:2rem;
									color:#fbf200;
								}
							}
							position:absolute;
						}

						.first-invest{
							top:-7rem;
							left:4rem;
						}

						.first-income{
							top:2.5rem;
							right:2rem;
						}
						.welfares{
							top:8rem;
							left:2rem;
						}
						.logins{
							right:2rem;
						}
					}


					.history{
						position:relative;
						width:300px;
						height:300px;
						margin:0 auto;
						.invest-type{
							position:absolute;
							text-align: left;
							p{
								font-size:14px;
								color:$white;
								margin-bottom: 0;
								span{
									color:#fbf200;
									font-size:24px;
								}
							}

						}
						.one{
							top:50px;
							left:60px;
						}
						.three{
							top:50px;
							right:60px;
						}
						.six{
							bottom:40px;
							left:30px;
						}
						.day-invest{
							bottom:40px;
							right:30px;
						}
						.center-words{
							z-index:100;
							color:$red;
							text-align: center;
							font-size:15px;
							p{
								margin-bottom:0;
								span{
									font-size:24px;
								}
							}
						}
						canvas{
							position:relative;
							z-index: 10;
						}
					}
				}
			}
		}
	}
}