.feedback{
	position: absolute;
	top:40px;
	left:0;
	right:0;
	bottom:0;
	background-color:#f8f8f8;
	padding-top:$f10;

	textarea{
		width:100%;
		font-size: $f12;
		height:150px;
		resize: none;
		border:1px solid #e4e4e4;
		border-radius:10px;
		padding:$f10;
		margin-bottom:4.5rem;
		outline: none;
	}
}