.setup{
	position:absolute;
	top:40px;
	left:0;
	right:0;
	bottom:45px;
	overflow-y:auto;
	ul{
		height:auto;
		background-color:$white;
		li{
			font-size:$f15;
			color:#666;
			border-bottom:1px solid #f1f1f1;
			height:5rem;
			line-height: 5rem;
			margin-bottom:1px;

			>.iconfont{
				font-size:$f15;
				margin-right:$f10;
				color:$red;
				display: inline-block;
				width:3rem;
				text-align: center;
				vertical-align: top;
			}
			.icon-shouji{
				font-size: 2rem;
			}
			.icon-suo,.icon-353245214507{
				font-size: 1.8rem;
			}

			.right-words{
				font-size: $f12;
				color:#6d6d6d;
				.iconfont{
					font-size: $f12;
					color:#6d6d6d;
				}
			}
		}
		.right-words{
			color:#6d6d6d;
			font-size: $f11;
		}
	}
}