.welfare2{
	margin-top:40px;
	padding-top:50px;
	padding-bottom:10px;
	background-color:#f8f8f8;
	
	.welfare-item{
		.welfare-state{
			width:5rem;
			height:4rem;
			background-image:url(#{$imgPath}/welfare-state.png);
			background-size:20rem 4rem;
			background-repeat:no-repeat;
			position:absolute;
			left:45%;
			top:30%;
			opacity: 0.8;
			-webkit-transform: scale(1.2);
			-moz-transform: scale(1.2);
			-ms-transform: scale(1.2);
			-o-transform: scale(1.2);
			transform: scale(1.2);
		}
		ul{
			width:100%;
			height:auto;
			li.state{
				width:100%;
				height:auto;
				position:relative;
				margin-bottom:$f10;
				color:#717171;
				img{
					width:100%;
					font-size: 0;
				}
				.welfare-content{
					position:absolute;
					left:0;
					top:0;
					width:100%;
					height:100%;
					padding:$f10 $f20;
					.fl,.fr{
						width:50%;
						height:100%;
					}
					.fl{
						border-right:1px solid #f1f1f1;
					}

					.welfare-nums{
						height:60%;
						width:100%;
						font-size:2.5rem;
						color:$red;
						padding-right: $f20;
						i{
							font-size: $f20;
						}
					}
					.welfare-from{
						height:40%;
						width:100%;
						padding-right: $f20;
						font-size: $f10;
					}
					.welfare-use{
						height:60%;
						width:100%;
						font-size:$f12;
						.welfare-function{
							width:100%;
							text-align: right;
						}
					}
					.welfare-duration{
						height:40%;
						width:100%;
						font-size: $f10;
						.durationTo{
							width:100%;
							text-align: right;
						}
					}
				}
			}
			li.used{
				color:#aaa;
				.welfare-content{
					.welfare-nums{
						color:#aaa;
					}
				}
				.welfare-state{
					background-position:0 0;
				}
			}
			li.overdue{
				.welfare-content{
					.welfare-nums{
						color:#aaa;
					}
				}
				.welfare-state{
					background-position:-5rem 0;
				}
			}
			li.alreay-pay{
				.welfare-content{
					.welfare-nums{
						color:#aaa;
					}
				}
				.welfare-state{
					background-position:-10rem 0;
				}
			}
			li.not-generate{
				.welfare-content{
					.welfare-nums{
						color:#aaa;
					}
				}
				.welfare-state{
					background-position:-15rem 0;
				}
			}
		}
	}

	.rate-voucher,.wealth-value{
		.welfare-content{
			.welfare-nums{
				span{
					font-size:$f15;
				}
			}
		}
	}
	.rate-voucher{
		ul li.state{
			.welfare-content{
				.welfare-nums{
					height:100%;
				}
				.welfare-duration{
					height:100%;
				}
			}

		}
	}


}