.discovery-box{
	margin-top:40px;
	background-color:#eee;
	.banner{
		img{
			width:100%;
			display: block;
		}
	}
	.options{
		margin-bottom:$f10;
		ul{
			width:100%;
			height:auto;
			background-color:$white;

			li{
				// display: block;
				width:100%;
				height:auto;
				border-bottom:1px solid #f2f2f2;
				border-right:1px solid #f2f2f2;
				background-color:$white;
				.option-item{
					-webkit-flex:1;
					flex:1;
					width:100%;
					border-right:1px solid #f2f2f2;
					padding:$f15 0 $f15 $f15;
				}
				.option-item:nth-child(2){
					-webkit-flex:1;
					flex:1;
					border-right:none;
				}
				.logo{
					display: block;
					// background-color:#ea4c4c;
					width:4rem;
					height:4rem;
					line-height: 3.8rem;
					text-align:center;
					margin-right:0.8rem;
					// font-size:$f18;
					border-radius:50%;
					color:$white;
					.iconfont{
						color:$red;
						font-size: 4rem;
					}
				}
				.intro{
					.title{
						margin-top: 0.5rem;
						font-size: $f14;
						color:#4d4d4d;
						display: block;
						line-height: $f15;
					}
					.abstract{
						margin-top: 0.3rem;
						font-size:$f10;
						color:#ababab;
						display: block;
						line-height: $f15;
					}
				}
			}
		}
	}


	.latest-news{
		border-bottom:1px solid #e6e6e6;
		background-color:$white;
		margin-bottom:40px;
		// margin-top: -10px;

		
		li{
			font-size:$f14;
			
			.date{
				font-size: $f12;
				bottom:$f18;
				color:#5a5a5a;
			}
			.abstract{
				line-height: $f18;
			}
		}
		li:nth-child(1){
			padding-top:$f20;
			margin:0 $f15 0;
		}
	}
}