.index-datas{
	position:absolute;
	top:0;
	left:0;
	right:0;
	bottom:0;
	background-image:url(#{$imgPath}/personal-bg.gif);
	background-size:cover;
	background-repeat:no-repeat;
	background-position:center center;

	.datas{
		position:absolute;
		top:50%;
		left:50%;
		-webkit-transform: translate(-50%,-50%);
		transform: translate(-50%,-50%);
	}
	li{
		// border-bottom:1px solid $white;
		padding:2rem 1rem 2rem 5rem;
		position:relative;
		.underline{
			width:100%;
			height:1px;
			background-image:-webkit-linear-gradient(to right,rgba(255,255,255,0),rgba(255,255,255,1),rgba(255,255,255,0));
			background-image:linear-gradient(to right,rgba(255,255,255,0),rgba(255,255,255,1),rgba(255,255,255,0));
			position:absolute;
			bottom:1px;
			left:0px;
		}
		h5{
			color:#fff600;
			font-size:$f15;
			margin-bottom:1rem;
		}
		p{
			color:$white;
			font-size:$f12;
			span{
				font-size:2rem;
				margin-right:5px;
			}
		}

	}
	.close-datas{
		width:4rem;
		height:4rem;
		border-radius:50%;
		border:2px solid #fff;
		position:relative;
		display: block;
		margin:7rem auto 0;
	}
	.close-datas::after{
		font-size:2.5rem;
		color:$white;
		display: block;
		position:absolute;
		top:50%;
		left:50%;
		-webkit-transform: translate(-50%,-50%);
		transform: translate(-50%,-50%);
		content:'\2715';
		font-weight: lighter;
	}
}