.investing{
	margin-top:40px;
	ul{
		margin-bottom:6rem;
		li{
			width:100%;
			border-bottom:1px solid #f2f2f2;
			height:auto ;
			padding:$f15 0;
			font-size:$f14;

			.balance{
				font-size:$f11;
				color:#7d7d7d;
				i{
					font-size:$f10;
				}
				.investNow{
					display: inline-block;
					border:1px solid $yellow;
					padding:2px;
					border-radius:3px;
					width:auto;
					height:auto;
					font-size:$f11;
					line-height: $f11;
					background-color:$white;
					color:$yellow;
					vertical-align: center;
					margin-left:0.5rem;

				}
			}
			.purchase{
				padding-top:0.8rem;
				padding-bottom:0.8rem;
				color:#292929;
				display: inline-block;
				width:100%;
				.fr{
					width:50%;
				}
				.purchase-money{
					border:none; 
					outline: none;
					height:$f14;
					font-size: $f12;
					line-height: $f14;
					text-align: right;
					width: 100%;
				}
			}
			.expact{
				font-size:$f12;
				color:#878787;
				i{
					font-size:$f10;
				}
			}
			.expact:after { 
				visibility:hidden; 
				display:block; 
				font-size:0; 
				content:""; 
				clear:both; 
				height:0;
			}
			.surplus{
				line-height: $f14;
				.surplus-money{
					font-size:$f11;
					i{
						font-size:$f10;
					}
				}
			}
			.red-bag{
				line-height: $f14;
				.bag-nums{
					font-size:$f13;
				}
			}
			.valid{
				color:$yellow;
			}
			.invalid{
				color:#b7b7b7;
			}
			.bags{
				padding-top:$f10;
				span{
					margin-bottom:0.5rem;
					padding:0.5rem $f10;
					margin-right:0.8rem;
					// height:25px;
					// line-height: 25px;
					border:1px solid #e5e5e5;
					border-radius:3px;
					color:#999;
					display: inline-block;
					text-align: center;
					font-size:$f13;
					i{
						font-size:$f12;
					}

				}
				.select{
					background-color:$red;
					color:$white;
					border:1px solid transparent;
				}
			}
			.raise-interest{
				line-height: $f14;
				.tickets{
					font-size:$f13;
				}
			}
			.rate-tickets{
				padding-top:$f10;
				
				span{
					margin-bottom:0.5rem;
					width:6rem;
					height:3rem;
					line-height: 3rem;
					margin-right:0.8rem;
					
					color:#999;
					display: inline-block;
					text-align: center;
					font-size:$f13;
					background-image:url(#{$imgPath}/tickets.png);
					background-size:12rem 3rem;
				}
				.select{
					background-position:-6rem 0;
					color:$white;
				}
			}
			.total{
				.total-money{
					font-size:$f13;
					color:$red;
					i{
						font-size:$f11;
					}
				}
			}
		}
	}
}